<script>
import QRCode from 'qrcode' // qrcode
export default {
  name: 'RouterPay',
  data () {
    return {
      payInfo: {},  // 
      timer: null, // 
      code: '',      // 
    }
  },
  computed: {
    orderId () {
      return this.$route.query.orderId;   // tradeorderId
    },

  },


  mounted () {
    // mountedgetPayInfo
    this.getPayInfo();
  },

  methods: {
    // 
    async getPayInfo () {
      // 1
      let result = await this.$API.reqGetPayInfo(this.orderId);
      // 2
      if (result.code == '200') {
        // 2.1
        this.payInfo = result.data;
      } else {
        // 2.2
        console.log(result.message);
      }
    },

    // element-uiqrcode
    async open () {
      let imgUrl = await QRCode.toDataURL(this.payInfo.codeUrl);
      // QRCode.toDataURLawaitpromise
      // 
      this.$alert(`<img src=${imgUrl}>`, '', {
        dangerouslyUseHTMLString: true,
        center: true,                     // 
        showCancelButton: true,           // 
        cancelButtonText: '', // 
        confirmButtonText: '',  // 
        showClose: false,                 // 
        beforeClose: (type, instance, done) => {
          //type|
          //instance
          //done

          if (type == 'cancel') {
            // this.timer
            alert('');
            clearInterval(this.timer);
            this.timer = null;
            done();
          }
          else {
            // this.timer
            // 
            // if (this.code == 200) {
            clearInterval(this.timer);
            this.timer = null;
            this.$msgbox.close();
            this.$router.push('/paysuccess');
            // }
          }
        }
      });

      // 
      // 
      if (!this.timer) {
        this.timer = setInterval(async () => {
          // 
          let result = await this.$API.reqPayStatus(this.orderId);
          console.log(result);
          // 
          if (result.code == 200) {
            // this.timer
            clearInterval(this.timer);
            this.timer = null;
            // 
            this.code = result.code;
            // 
            this.$msgbox.close();
            this.$router.push('/paysuccess');
          }
        }, 2000);
      }
    }
  },
}
</script>

