<template>
  <div class="register-container">
    <!--  -->
    <div class="register">
      <h3>
        <span class="go"> <a href="login.html"
             target="_blank"></a>
        </span>
      </h3>
      <div class="content">
        <label>:</label>
        <input type="text"
               placeholder=""
               v-model="phone">
        <span class="error-msg"
              v-show="phoneError!=''">{{phoneError}}</span>
      </div>
      <div class="content">
        <label>:</label>
        <input type="text"
               placeholder=""
               v-model="code">
        <button style="width:90px;height:38px"
                @click="getCode"></button>
        <span class="error-msg"></span>
      </div>
      <div class="content">
        <label>:</label>
        <input type="password"
               placeholder=""
               v-model="password">
        <span class="error-msg"></span>
      </div>
      <div class="content">
        <label>:</label>
        <input type="password"
               placeholder=""
               v-model="password2">
        <span class="error-msg"
              v-show="password2Error!=''">{{password2Error}}</span>
      </div>
      <div class="controls">
        <input name="m1"
               type="checkbox"
               v-model="agree">
        <span></span>
        <span class="error-msg"></span>
      </div>
      <div class="btn">
        <button @click="userRegister"></button>
      </div>
    </div>

    <!--  -->
    <div class="copyright">
      <ul>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ul>
      <div class="address">6</div>
      <div class="beian">ICP19006430
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'RouterRegister',
  data () {
    return {
      // 
      phone: '',
      code: '',
      password: '',
      password2: '',
      agree: false,
    }
  },

  methods: {
    // 
    async getCode () {
      try {
        await this.$store.dispatch('getCode', this.phone);
        this.code = this.$store.state.user.testCodeFromServer;
      }
      catch (error) {
        // 3-- 
        alert(error.message);
      }
    },

    // 
    async userRegister () {
      if (this.phoneError != '' || this.password2Error != '' || this.agree == false) {
        alert('');
        return;
      }
      try {
        // data
        const { phone, code, password } = this;
        await this.$store.dispatch("userRegister", { phone, code, password });
        //
        this.$router.push("/login");
      } catch (error) {
        alert(error.message);
      }
    },
  },

  computed: {
    phoneError () {
      let phoneReg = /^1[3-9][0-9]{9}$/;
      if (phoneReg.test(this.phone) || this.phone == '')
        return '';
      else return '';
    },
    password2Error () {
      if (this.password2 != this.password)
        return '';
      let phoneReg = /^1[3-9][0-9]{9}$/;
      if (phoneReg.test(this.password2) || this.password2 == '')
        return '';
      else return '';
    }


  }
}

</script>

<style lang="less" scoped>
.register-container {
  .register {
    width: 1200px;
    height: 445px;
    border: 1px solid rgb(223, 223, 223);
    margin: 0 auto;

    h3 {
      background: #ececec;
      margin: 0;
      padding: 6px 15px;
      color: #333;
      border-bottom: 1px solid #dfdfdf;
      font-size: 20.04px;
      line-height: 30.06px;

      span {
        font-size: 14px;
        float: right;

        a {
          color: #e1251b;
        }
      }
    }

    div:nth-of-type(1) {
      margin-top: 40px;
    }

    .content {
      padding-left: 390px;
      margin-bottom: 18px;
      position: relative;

      label {
        font-size: 14px;
        width: 96px;
        text-align: right;
        display: inline-block;
      }

      input {
        width: 270px;
        height: 38px;
        padding-left: 8px;
        box-sizing: border-box;
        margin-left: 5px;
        outline: none;
        border: 1px solid #999;
      }

      img {
        vertical-align: sub;
      }

      .error-msg {
        position: absolute;
        top: 100%;
        left: 495px;
        color: red;
      }
    }

    .controls {
      text-align: center;
      position: relative;

      input {
        vertical-align: middle;
      }

      .error-msg {
        position: absolute;
        top: 100%;
        left: 495px;
        color: red;
      }
    }

    .btn {
      text-align: center;
      line-height: 36px;
      margin: 17px 0 0 55px;

      button {
        outline: none;
        width: 270px;
        height: 36px;
        background: #e1251b;
        color: #fff !important;
        display: inline-block;
        font-size: 16px;
      }
    }
  }

  .copyright {
    width: 1200px;
    margin: 0 auto;
    text-align: center;
    line-height: 24px;

    ul {
      li {
        display: inline-block;
        border-right: 1px solid #e4e4e4;
        padding: 0 20px;
        margin: 15px 0;
      }
    }
  }
}
</style>