<template>
  <div class="trade-container">
    <h3 class="title"></h3>
    <div class="content">
      <h5 class="receive"></h5>
      <div class="address clearFix"
           v-for="(address) in userAddress"
           :key="address.id">
        <span class="username"
              :class="{selected:address.isDefault==1}">{{address.consignee}}</span>
        <p @click="changeDefault(address,userAddress)">
          <span class="s1">{{address.fullAddress}}</span>
          <span class="s2">{{address.phoneNum}}</span>
          <span class="s3"
                v-show="address.isDefault==1"></span>
        </p>
      </div>
      <div class="line"></div>
      <h5 class="pay"></h5>
      <div class="address clearFix">
        <span class="username selected"></span>
        <span class="username"
              style="margin-left:5px;"></span>

      </div>
      <div class="line"></div>
      <h5 class="pay"></h5>
      <div class="way">
        <h5></h5>
        <div class="info clearFix">
          <span class="s1"></span>
          <p>81009:00-15:00</p>
        </div>
      </div>
      <div class="detail"
           v-for="(order) in userOrderInfo.detailArrayList"
           :key="order.skuId">
        <h5></h5>
        <ul class="list clearFix">
          <li>
            <img :src="order.imgUrl"
                 alt=""
                 style="width:100px;height:100px;">
          </li>
          <li>
            <p>
              {{order.skuName}}</p>
            <h4>7</h4>
          </li>
          <li>
            <h3>{{order.orderPrice}}.00</h3>
          </li>
          <li>X{{order.skuNum}}</li>
          <li></li>
        </ul>
      </div>
      <div class="bbs">
        <h5></h5>
        <textarea placeholder=""
                  class="remarks-cont"
                  v-model="msg"></textarea>

      </div>
      <div class="line"></div>
      <div class="bill">
        <h5></h5>
        <div>  </div>
        <h5>/</h5>
      </div>
    </div>
    <div class="money clearFix">
      <ul>
        <li>
          <b><i>{{userOrderInfo.totalNum}}</i></b>
          <span>{{userOrderInfo.totalAmount}}.00</span>
        </li>
        <li>
          <b></b>
          <span>0.00</span>
        </li>
        <li>
          <b></b>
          <span>0.00</span>
        </li>
      </ul>
    </div>
    <div class="trade">
      <div class="price">:<span>{{userOrderInfo.totalAmount}}.00</span></div>
      <div class="receiveInfo">
        :
        <span>{{userDefaultAddress.fullAddress}}</span>
        <span>{{userDefaultAddress.consignee}}</span>
        <span>{{userDefaultAddress.phoneNum}}</span>
      </div>
    </div>
    <div class="sub clearFix">
      <a class="subBtn"
         @click="submitOrder"></a>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'RouterTrade',
  data () {
    return {
      msg: '',      // 
      orderId: '',  // 
    }
  },
  mounted () {
    this.$store.dispatch('getUserAddress'); // 
    this.$store.dispatch('getOrderInfo');   // 
  },

  computed: {
    ...mapState({
      userAddress: state => state.trade.userAddress,
      userOrderInfo: state => state.trade.orderInfo,
    }),

    // 
    userDefaultAddress () {
      // 1
      return this.userAddress.find(item => item.isDefault == 1) || {};
    }
  },

  methods: {
    // 
    changeDefault (address, userAddress) {
      userAddress.forEach((element) => {
        element.isDefault = 0;
      });
      address.isDefault = 1;
    },

    // 
    async submitOrder () {
      // 1
      let tradeNo = this.userOrderInfo.tradeNo;
      let data = {
        consignee: this.userDefaultAddress.consignee,
        consigneeTel: this.userDefaultAddress.phoneNum,
        deliveryAddress: this.userDefaultAddress.fullAddress,
        paymentWay: "ONLINE",
        orderComment: this.msg,
        orderDetailList: this.userOrderInfo.detailArrayList,    // 
      }
      // 2
      let result = await this.$API.reqSubmitOrder(tradeNo, data);
      // 3
      if (result.code == '200') {
        // 3.1
        this.orderId = result.data;
        this.$router.push('/pay?orderId=' + this.orderId);
      } else {
        ///3.2
        console.log(result.message);
      }
    }
  },
}
</script>

