// search 
import { reqGetSearchInfo } from "../../api";
const actions = {
  //
  async getSearchList({ commit }, params = {}) {
    // paramsactionnull
    let result = await reqGetSearchInfo(params); // apireqGetSearchInfo
    if (result.code == 200) {
      commit("GETSEARCHLIST", result.data); // commit
    }
  },
};
const mutations = {
  GETSEARCHLIST(state, searchList) {
    state.searchList = searchList;
  },
};
const state = {
  searchList: {}, // 
};

// getters
// gettersstate.search.searchList.goodsList,
const getters = {
  goodsList(state) {
    // statesearchstate
    // goodsListundefined
    // goodsListv-for
    return state.searchList.goodsList || [];
  },
  trademarkList(state) {
    return state.searchList.trademarkList || [];
  },
  attrsList(state) {
    return state.searchList.attrsList || [];
  },
};

export default {
  actions,
  mutations,
  state,
  getters,
};
