// axios
import axios from "axios";

//1axioscreateaxios
//2requestaxios
const requests = axios.create({
  //
  //api
  baseURL: "/mock",
  //5S
  timeout: 5000,
});

// 
requests.interceptors.request.use((config) => {
  //configheaders
  return config;
});

// 
requests.interceptors.response.use(
  (res) => {
    //
    return res.data;
  },
  (error) => {
    //
    return Promise.reject(error);
  }
);

export default requests;
