import Vue from "vue";
import App from "./App.vue";
import router from "./router"; // router
import store from "./store"; // store
import "@/mock/mockServe"; // mockServe.js
import "swiper/css/swiper.css"; // Swiper
import { MessageBox } from "element-ui"; // element-uimessagebox
import VueLazyload from "vue-lazyload"; // VueLazyload
import pluginA from "@/plugins/pluginA"; // 
// api
import * as API from "@/api";

// 
import TypeNav from "./components/TypeNav";
import CarouselA from "./components/Carousel";
import PaginationA from "./components/Pagination";
Vue.component(TypeNav.name, TypeNav);
Vue.component(CarouselA.name, CarouselA);
Vue.component(PaginationA.name, PaginationA);

// messageboxvue
Vue.prototype.$msgbox = MessageBox;
Vue.prototype.$alert = MessageBox.alert;

//vueLazyload
import imgLoading from "@/assets/logo.png";
Vue.use(VueLazyload, {
  loading: imgLoading,
});
// pluginA
Vue.use(pluginA, {
  name: "upper",
});

Vue.config.productionTip = false;
new Vue({
  render: (h) => h(App),
  router,
  store,
  beforeCreate() {
    Vue.prototype.$bus = this; // 
    Vue.prototype.$API = API; // apivue
  },
}).$mount("#app");
