<template>
  <div class="spec-preview">
    <img :src="imgObj.imgUrl" />
    <!--  -->
    <div class="event"
         @mousemove="handler"></div>
    <div class="big">
      <img :src="imgObj.imgUrl"
           ref="big" />
    </div>
    <!--  -->
    <div class="mask"
         ref="mask"></div>
  </div>
</template>

<script>
export default {
  name: "ZoomA",
  props: ['skuImageList'],  // detail
  data () {
    return {
      currendIndex: 0,
    }
  },
  computed: {
    // skuImageListskuImageList[0].imgUrlundefined
    imgObj () {
      return this.skuImageList[this.currendIndex] || {};
    }
  },

  mounted () {
    // 
    this.$bus.$on('getIndex', (index) => {
      console.log(11)
      this.currendIndex = index;
    })
  },

  methods: {
    handler (event) {
      let mask = this.$refs.mask;
      let big = this.$refs.big;

      // 00
      let maskNewLeft = event.offsetX - mask.offsetWidth / 2;
      let maskNewTop = event.offsetY - mask.offsetHeight / 2;

      // 
      // mask0mask
      // mask0mask
      if (maskNewLeft < 0)
        maskNewLeft = 0;
      if (maskNewLeft >= mask.offsetWidth)
        maskNewLeft = mask.offsetWidth;
      if (maskNewTop < 0)
        maskNewTop = 0;
      if (maskNewTop >= mask.offsetHeight)
        maskNewTop = mask.offsetHeight;

      mask.style.left = maskNewLeft + 'px';
      mask.style.top = maskNewTop + 'px';

      // bigbigbig
      // 
      big.style.left = -2 * maskNewLeft + 'px';
      big.style.top = -2 * maskNewTop + 'px';

    }
  },
}
</script>

<style lang="less">
.spec-preview {
  position: relative;
  width: 400px;
  height: 400px;
  border: 1px solid #ccc;

  img {
    width: 100%;
    height: 100%;
  }

  .event {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 998;
  }

  .mask {
    width: 50%;
    height: 50%;
    background-color: rgba(0, 255, 0, 0.3);
    position: absolute;
    left: 0;
    top: 0;
    display: none;
  }

  .big {
    width: 100%;
    height: 100%;
    position: absolute;
    top: -1px;
    left: 100%;
    border: 1px solid #aaa;
    overflow: hidden;
    z-index: 998;
    display: none;
    background: white;

    img {
      width: 200%;
      max-width: 200%;
      height: 200%;
      position: absolute;
      left: 0;
      top: 0;
    }
  }

  .event:hover ~ .mask,
  .event:hover ~ .big {
    display: block;
  }
}
</style>