// detail 
import { getUUID } from "@/utils/uuid_token";
import { reqGetGoodInfo, reqAddOrUpdateCart } from "../../api";
const actions = {
  // 
  async getGoodInfo({ commit }, skuId) {
    let result = await reqGetGoodInfo(skuId); // apireqGetGoodInfo
    if (result.code == 200) {
      commit("GETGOODINFO", result.data); // commit
    }
  },

  // 
  async addOrUpdateCart({ commit }, { skuId, skuNum }) {
    let result = await reqAddOrUpdateCart(skuId, skuNum); // apireqGetGoodInfo
    if (result.code == 200) {
      commit("NOUSE"); // commitcommit
      return "ok";
    } else {
      return Promise.reject(new Error("fail"));
    }
  },
};
const mutations = {
  GETGOODINFO(state, goodInfo) {
    state.goodInfo = goodInfo;
  },
  NOUSE() {},
};
const state = {
  goodInfo: {}, // 
  uuid_token: getUUID(),
};

// getters
const getters = {
  // 
  categoryView(state) {
    // statesearchstate
    return state.goodInfo.categoryView || {};
  },
  // 
  skuInfo(state) {
    return state.goodInfo.skuInfo || {};
  },
  // 
  spuSaleAttrList(state) {
    return state.goodInfo.spuSaleAttrList || [];
  },
};

export default {
  actions,
  mutations,
  state,
  getters,
};
