import { reqGetCartList } from "../../api";
import { reqDeleteCart } from "../../api";
import { reqUpdateCartChecked } from "../../api";
const actions = {
  // 
  async getCartList({ commit }) {
    let result = await reqGetCartList(); // apireqGetCartList
    if (result.code == 200) {
      commit("GETCARTLIST", result.data); // commit
    }
  },

  // 
  async deleteCart({ commit }, skuId) {
    let result = await reqDeleteCart(skuId); // apireqDeleteCart
    if (result.code == 200) {
      commit("NOUSE"); // commitvuexcommit
      return "ok";
    } else {
      return Promise.reject(new Error("fail"));
    }
  },

  // 
  async updateCartChecked({ commit }, { skuId, isChecked }) {
    let result = await reqUpdateCartChecked(skuId, isChecked); // apireqUpdateCartChecked
    if (result.code == 200) {
      commit("NOUSE"); // commitvuexcommit
      return "ok";
    } else {
      return Promise.reject(new Error("fail"));
    }
  },

  // 
  async deleteAllCheckedCart({ dispatch, getters }) {
    let PromiseAll = [];
    getters.cartList.cartInfoList.forEach((element) => {
      if (element.isChecked == 1) {
        let result = dispatch("deleteCart", element.skuId);
        // promise
        PromiseAll.push(result);
      }
    });
    //p1p2.
    //
    return Promise.all(PromiseAll);
  },

  // 
  // gettersidupdateCartCheckedactions
  async checkedAllCart({ dispatch, getters }, isChecked) {
    let PromiseAll = [];
    getters.cartList.cartInfoList.forEach((element) => {
      let result = dispatch("updateCartChecked", {
        skuId: element.skuId,
        isChecked: isChecked,
      });
      // promise
      PromiseAll.push(result);
    });
    //p1p2.
    //
    return Promise.all(PromiseAll);
  },
};

const mutations = {
  GETCARTLIST(state, cartList) {
    state.cartList = cartList;
  },

  NOUSE() {},
};
const state = {
  cartList: [], // 
};

const getters = {
  cartList(state) {
    // 
    return state.cartList[0] || {};
  },
};

export default {
  actions,
  mutations,
  state,
  getters,
};
