<template>
  <div class="cart">
    <h4></h4>
    <div class="cart-main">
      <div class="cart-th">
        <div class="cart-th1"></div>
        <div class="cart-th2"></div>
        <div class="cart-th3"></div>
        <div class="cart-th4"></div>
        <div class="cart-th5"></div>
        <div class="cart-th6"></div>
      </div>
      <div class="cart-body">
        <ul class="cart-list"
            v-for="(cart) in cartInfoList"
            :key=cart.id>
          <li class="cart-list-con1">
            <input type="checkbox"
                   name="chk_list"
                   :checked="cart.isChecked==1"
                   @change="updateChecked(cart,$event)">
          </li>
          <li class="cart-list-con2">
            <img :src="cart.imgUrl">
            <div class="item-msg">{{cart.skuName}}</div>
          </li>
          <li class="cart-list-con4">
            <span class="price">{{cart.skuPrice || 0}}</span>
          </li>
          <li class="cart-list-con5">
            <a href="javascript:void(0)"
               class="mins"
               @click="changeNum('decrease',-1,cart)">-</a>
            <input autocomplete="off"
                   type="text"
                   minnum="1"
                   onkeyup="value=value.replace(/[^\d]/g,'')"
                   max="10000"
                   class="itxt"
                   :value="cart.skuNum"
                   @change="changeNum('change',$event.target.value*1,cart)">
            <a href="javascript:void(0)"
               class="plus"
               @click="changeNum('increase',1,cart)">+</a>
          </li>
          <li class="cart-list-con6">
            <span class="sum">{{cart.skuPrice * cart.skuNum}}</span>
          </li>
          <li class="cart-list-con7">
            <a href="#none"
               class="sindelet"
               @click="deleteCartOfShop(cart.skuId)"></a>
            <br>
            <a href="#none"></a>
          </li>
        </ul>
      </div>
    </div>
    <div class="cart-tool">
      <div class="select-all">
        <input class="chooseAll"
               type="checkbox"
               :checked="isAllCheck&&cartInfoList.length>0"
               @click="checkedAllCart">
        <span></span>
      </div>
      <div class="option">
        <a @click="deleteAllCheckedCart"></a>
        <a href="#none"></a>
        <a href="#none"></a>
      </div>
      <div class="money-box">
        <div class="chosed">
          <span>0</span>
        </div>
        <div class="sumprice">
          <em> </em>
          <i class="summoney">{{totalPrice}}</i>
        </div>
        <div class="sumbtn">
          <router-link class="sum-btn"
                       to="/trade"></router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import _ from 'lodash';
export default {
  name: 'RouterShopCart',
  mounted () {
    this.getData();
  },
  computed: {
    // 
    ...mapGetters(['cartList']),

    // 
    cartInfoList () {
      return this.cartList.cartInfoList || [];
    },

    // 
    totalPrice () {
      let sum = 0;
      this.cartInfoList.forEach(element => {
        sum = sum + element.skuPrice * element.skuNum;
      });
      return sum;
    },

    // 
    isAllCheck () {
      return this.cartInfoList.every(item => item.isChecked == 1)
      // everyevery
    },
  },


  methods: {
    // 
    getData () {
      this.$store.dispatch('getCartList');
    },

    // 1
    // 2
    // 3
    // 
    changeNum: _.throttle(async function (type, disNum, cart) {
      // 1
      if (type == 'increase')
        disNum = 1;
      else if (type == 'decrease')
        disNum = cart.skuNum > 1 ? -1 : 0;   // -11-110
      else if (type == 'change') {
        if (isNaN(disNum) || disNum < 1) {  // 
          disNum = 0;
        } else {
          disNum = parseInt(disNum) - cart.skuNum;
        }
      }
      // 2
      try {
        await this.$store.dispatch('addOrUpdateCart', { skuId: cart.skuId, skuNum: disNum });
        this.getData();
      }
      catch (error) {
        // 3-- 
        alert(error.message);
      }
    }, 1100),

    // async changeNum (type, disNum, cart) {
    //   // 1
    //   if (type == 'increase')
    //     disNum = 1;
    //   else if (type == 'decrease')
    //     disNum = cart.skuNum > 1 ? -1 : 0;   // -11-110
    //   else if (type == 'change') {
    //     if (isNaN(disNum) || disNum < 1) {  // 
    //       disNum = 0;
    //     } else {
    //       disNum = parseInt(disNum) - cart.skuNum;
    //     }
    //   }
    //   // 2
    //   try {
    //     await this.$store.dispatch('addOrUpdateCart', { skuId: cart.skuId, skuNum: disNum });
    //     this.getData();
    //   }
    //   catch (error) {
    //     // 3-- 
    //     alert(error.message);
    //   }
    // },


    // 
    async deleteCartOfShop (id) {
      try {
        await this.$store.dispatch('deleteCart', id);
        this.getData();
      }
      catch (error) {
        // 3-- 
        alert(error.message);
      }
    },

    // 
    async updateChecked (cart, e) {
      try {
        // string0/1true/false
        let checked = e.target.checked ? '1' : '0';
        await this.$store.dispatch('updateCartChecked', { skuId: cart.skuId, isChecked: checked });
        this.getData();
      }
      catch (error) {
        // 3-- 
        alert(error.message);
      }
    },

    // 
    async deleteAllCheckedCart () {
      try {
        await this.$store.dispatch('deleteAllCheckedCart');
        this.getData();
      }
      catch (error) {
        // 3-- 
        alert(error.message);
      }
    },

    // 
    async checkedAllCart (e) {
      try {
        let isChecked = e.target.checked ? "1" : "0";
        await this.$store.dispatch('checkedAllCart', isChecked);
        this.getData();
      }
      catch (error) {
        // 3-- 
        alert(error.message);
      }
    }
  },
}
</script>

