<template>
  <div class="pagination">
    <!--  -->
    <button @click="prePageClick"></button>
    <!-- start11start2'...' -->
    <button v-if="startNumAndEndNum.start > 1"
            @click="pageOneClick"
            :class="{active:pageNo==1}">1</button>
    <button v-if="startNumAndEndNum.start > 2"></button>

    <!--  -->
    <!-- continues -->
    <button v-for="(index) in continues"
            :key="index"
            @click="pageClick(startNumAndEndNum.start + index - 1)"
            :class="{active:pageNo==startNumAndEndNum.start + index - 1}">{{startNumAndEndNum.start + index - 1}}</button>

    <!--  -->
    <!-- endtotalPage'...'endtotalPage-1totalPage -->
    <button v-if="startNumAndEndNum.end < totalPage -1"></button>
    <button v-if="startNumAndEndNum.end < totalPage"
            @click="pageEndClick"
            :class="{active:pageNo==totalPage}">{{totalPage}}</button>
    <button @click="nextPageClick"></button>

    <button style="margin-left: 30px"> {{total}} </button>
  </div>
</template>

<script>
export default {
  name: "PaginationA",
  props: ['pageNo', 'pageSize', 'total', 'continues'],
  computed: {
    // 
    totalPage () {
      return Math.ceil(this.total / this.pageSize);	// 
    },

    // 
    startNumAndEndNum () {
      const { pageNo, totalPage, continues } = this;
      let start = 0, end = 0;

      // 1
      if (continues >= totalPage) {
        start = 1; end = totalPage;
      }
      // 2
      else {
        start = pageNo - parseInt(continues / 2);
        end = pageNo + parseInt(continues / 2);
        // 32start1
        if (start < 1) {
          start = 1;
          end = continues;
        }
        // 4 totalPage - 2endtotalPage
        if (end > totalPage) {
          start = totalPage - continues + 1;
          end = totalPage;
        }
      }
      return { start, end };
    }
  },
  methods: {
    // 
    prePageClick () {
      if (this.pageNo > 1)
        this.$emit('getPageNo', this.pageNo - 1);
    },

    // 
    pageOneClick () {
      this.$emit('getPageNo', 1);
    },

    // 
    pageClick (index) {
      this.$emit('getPageNo', index);
    },

    // 
    pageEndClick () {
      this.$emit('getPageNo', this.totalPage);
    },

    // 
    nextPageClick () {
      if (this.pageNo < this.totalPage)
        this.$emit('getPageNo', this.pageNo + 1);
    },
  },
}
</script>

<style lang="less" scoped>
.pagination {
  text-align: center;
  button {
    margin: 0 5px;
    background-color: #f4f4f5;
    color: #606266;
    outline: none;
    border-radius: 2px;
    padding: 0 4px;
    vertical-align: top;
    display: inline-block;
    font-size: 13px;
    min-width: 35.5px;
    height: 28px;
    line-height: 28px;
    cursor: pointer;
    box-sizing: border-box;
    text-align: center;
    border: 0;

    &[disabled] {
      color: #c0c4cc;
      cursor: not-allowed;
    }

    &.active {
      cursor: not-allowed;
      background-color: #e1251b;
      color: #fff;
    }
  }
}
</style>
