// API
import requests from "./request";
import mockRequests from "./mockAjax";

//
//api/product/getBaseCategoryList get 
//axiosPromise
export const reqCategoryList = () =>
  requests({ url: "/product/getBaseCategoryList", method: "get" });

// banner
export const reqGetBannerList = () =>
  mockRequests({ url: "/banner", method: "get" });

// floor
export const reqGetFloorList = () =>
  mockRequests({ url: "/floor", method: "get" });

//    /api/list   post    
// 
// {
//   "category1Id": "61",
//   "category2Id": "61",
//   "category3Id": "61",
//   "categoryName": "",
//   "keyword": "",
//   "order": "1:desc",
//   "pageNo": 1,
//   "pageSize": 10,
//   "props": ["1:1700-2799:", "2:6.65-6.74:"],
//   "trademark": "4:"
// }
// paramsnull
export const reqGetSearchInfo = (data) =>
  requests({ url: "/list", method: "post", data });

//    /api/item/{skuId}   get    skuId
export const reqGetGoodInfo = (skuId) =>
  requests({ url: `/item/${skuId}`, method: "get" });

//    /api/cart/addToCart/{skuId}/ {skuNum}  post    skuId skuNum
export const reqAddOrUpdateCart = (skuId, skuNum) =>
  requests({ url: `/cart/addToCart/${skuId}/${skuNum}`, method: "post" });

// 
// URL:/api/cart/cartList method:get
export const reqGetCartList = () =>
  requests({ url: "/cart/cartList ", method: "get" });

// 
// URL:/api/cart/deleteCart/{skuId}   method:DELETE  skuId
export const reqDeleteCart = (skuId) =>
  requests({ url: `/cart/deleteCart/${skuId}`, method: "DELETE" });

// 
// URL:/api/cart/checkCart/{skuId}/{isChecked}   method:get   skuId,isChecked
export const reqUpdateCartChecked = (skuId, isChecked) =>
  requests({ url: `/cart/checkCart/${skuId}/${isChecked}`, method: "get" });

