import Vue from "vue";
import VueRouter from "vue-router";
Vue.use(VueRouter);
import store from "@/store"; // store

// NavigationDuplicated
// -------------------------------------------------------------------------------
//push
const originPush = VueRouter.prototype.push;
const originReplace = VueRouter.prototype.replace;
//pushpush
VueRouter.prototype.push = function (location, onResolved, onRejected) {
  //location this.$router.push
  if (onResolved === undefined && onRejected === undefined) {
    //
    return originPush.call(this, location).catch(() => {});
  } else {
    //
    return originPush.call(this, location, onResolved, onRejected);
  }
};
VueRouter.prototype.replace = function (location, onResolved, onRejected) {
  //location this.$router.replace

  if (onResolved === undefined && onRejected === undefined) {
    //
    return originReplace.call(this, location).catch(() => {});
  } else {
    //
    return originReplace.call(this, location, onResolved, onRejected);
  }
};
// -------------------------------------------------------------------------------

//
import Login from "../pages/Login";
import Register from "../pages/Register";
import Search from "../pages/Search";
import Detail from "../pages/Detail";
import AddCartSuccess from "../pages/AddCartSuccess";
import ShopCart from "../pages/ShopCart";
import Trade from "../pages/Trade";
import Pay from "../pages/Pay";
import PaySuccess from "../pages/PaySuccess";
import Center from "../pages/Center";

// 
import MyOrder from "../pages/Center/myOrder";
import GroupOrder from "../pages/Center/groupOrder";

//
const router = new VueRouter({
  routes: [
    {
      path: "/home",
      name: "RouterHome", // namepathrouterlink
      component: () => import("@/pages/Home"),
      meta: {
        show: true, // footer
      },
    },
    {
      path: "/login",
      name: "RouterLogin",
      component: Login,
      meta: {
        show: false,
      },
    },
    {
      path: "/register",
      name: "RouterRegister",
      component: Register,
      meta: {
        show: false,
      },
    },
    {
      path: "/search/:keyword?",
      name: "RouterSearch",
      component: Search,
      meta: {
        show: true,
      },
    },
    {
      path: "/detail/:skuid", // 
      name: "RouterDetail",
      component: Detail,
      meta: {
        show: true,
      },
    },
    {
      path: "/addcartsuccess",
      name: "RouterAddCartSuccess",
      component: AddCartSuccess,
      meta: {
        show: true,
      },
    },
    {
      path: "/shopcart",
      name: "RouterShopCart",
      component: ShopCart,
      meta: {
        show: true,
      },
    },
    {
      path: "/trade",
      name: "RouterTrade",
      component: Trade,
      meta: {
        show: true,
      },
      // trade
      beforeEnter: (to, from, next) => {
        // tradeshopcart
        if (from.path == "/shopcart") {
          next();
        } else {
          next(false);
        }
      },
    },
    {
      path: "/pay",
      name: "RouterPay",
      component: Pay,
      meta: {
        show: true,
      },
      // pay
      beforeEnter: (to, from, next) => {
        // paytrade
        if (from.path == "/trade") {
          next();
        } else {
          next(false);
        }
      },
    },
    {
      path: "/paysuccess",
      name: "RouterPaySuccess",
      component: PaySuccess,
      meta: {
        show: true,
      },
    },
    {
      path: "/center",
      name: "RouterCenter",
      component: Center,
      meta: {
        show: true,
      },
      // 
      children: [
        {
          path: "myorder",
          name: "RouterMyOrder",
          component: MyOrder,
        },
        {
          path: "grouporder",
          name: "RouterGroupOrder",
          component: GroupOrder,
        },
        // centermyorder
        {
          path: "/center",
          redirect: "/center/myorder",
        },
      ],
    },
    // 
    {
      path: "*",
      redirect: "/home",
    },
  ],

  // 
  scrollBehavior() {
    // 
    return { y: 0 };
  },
});

// 
router.beforeEach(async (to, from, next) => {
  //to
  //from
  //next;   next():   next(path):     next(false)

  // 1
  // 2actionstate
  // mounted
  // mountedvuex
  // searchstatesearch

  // tokentoken
  let token = store.state.user.token;
  // state
  let name = store.state.user.userInfo.name;
  if (token) {
    // 1
    if (to.path == "/login") {
      // 1
      next("/home");
      // console.log(111);  // 
    } else {
      // 2state
      if (name) {
        // 2
        next();
        // console.log(222);
      } else {
        // 2
        try {
          await store.dispatch("getUserInfo");
          next();
          // console.log(333);
        } catch (error) {
          // 2token
          store.dispatch("userLogout");
          next("/login");
        }
      }
    }
  } else {
    // 2
    let toPath = to.path;
    if (
      to.path.indexOf("trade") != -1 ||
      to.path.indexOf("pay") != -1 ||
      to.path.indexOf("center") != -1
    ) {
      // 2.1tradepaypaysuccesscenter
      next("/login?redirect=" + toPath);
    }
    // 2.2
    else {
      next();
    }
  }
});

export default router;
